@import "normalize.css";
@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("MullerRegular"), url("fonts/MullerRegular.woff2") format("woff2"), url("fonts/MullerRegular.woff") format("woff");
}
@font-face {
  font-family: "Muller";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local("MullerRegularItalic"), url("fonts/MullerRegularItalic.woff2") format("woff2"), url("fonts/MullerRegularItalic.woff") format("woff");
}
@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("MullerBold"), url("fonts/MullerBold.woff2") format("woff2"), url("fonts/MullerBold.woff") format("woff");
}
@font-face {
  font-family: "Muller";
  font-style: italic;
  font-weight: 300;
  font-display: swap;
  src: local("MullerLightItalic"), url("fonts/MullerLightItalic.woff2") format("woff2"), url("fonts/MullerLightItalic.woff") format("woff");
}
@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("MullerMedium"), url("fonts/MullerMedium.woff2") format("woff2"), url("fonts/MullerMedium.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("OpenSansRegular"), url("fonts/OpenSansRegular.woff2") format("woff2"), url("fonts/OpenSansRegular.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

label {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
svg {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Muller", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  color: #121723;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1310px;
  padding: 0 100px;
  margin: 0 auto;
}

.btn {
  display: flex;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
}

.section-title {
  font-size: 48px;
  line-height: 48px;
  font-weight: 400;
}

@-webkit-keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.stop-scroll {
  overflow: hidden;
}

@media (any-hover: hover) {
  .pop-up__close-btn:hover .pop-up__close-btn-svg circle {
    stroke: #6D31EE;
  }
  .pop-up__close-btn:hover .pop-up__close-btn-svg path {
    stroke: #6D31EE;
  }
  .header__tablet-btn:hover {
    background-color: #121723;
    color: white;
  }
  .header__desktop-btn:hover {
    background-color: #121723;
    color: white;
  }
  .header__broadcast-btn:hover .play-path {
    fill: #6D31EE;
  }
  .header__broadcast-btn:hover .pause-path {
    fill: #6D31EE;
  }
  .header__ac-trigger:hover .header__ac-svg {
    color: #6D31EE;
  }
  .search__submit:hover svg path {
    stroke: #AA9DFA;
  }
  .search__btn:hover svg path {
    stroke: #AA9DFA;
  }
  .hero__btn:hover {
    background-color: #AA9DFA;
  }
  .secondary-broadcast__button:hover .play-path {
    fill: #6D31EE;
  }
  .secondary-broadcast__button:hover .pause-path {
    fill: #6D31EE;
  }
  .primary-broadcast__button:hover .play-path {
    fill: #6D31EE;
  }
  .primary-broadcast__button:hover .pause-path {
    fill: #6D31EE;
  }
  .podcasts__btn:hover {
    background-color: #6D31EE;
    color: white;
  }
  .podcast__btn:hover .podcast__btn-svg path {
    fill: #6D31EE;
  }
  .podcast__btn:hover .podcast__btn-svg circle {
    stroke: #6D31EE;
  }
  .podcast__btn:hover .pause-path {
    fill: #6D31EE;
  }
  .broadcast:hover .broadcast__img {
    opacity: 0.7;
  }
  .broadcast__img:hover {
    opacity: 0.7;
  }
  .choices__inner:hover {
    background-color: #F5F6F7;
    border-color: #F5F6F7;
  }
  .tab__btn:hover {
    background-color: #AA9DFA;
    color: white;
  }
  .tab__socials-svg:hover path {
    fill: #6D31EE;
  }
  .guests__ac-trigger:hover .guests__ac-name {
    color: #AA9DFA;
  }
  .guests__ac-trigger:hover .guests__ac-svg path {
    stroke: #AA9DFA;
  }
  .guests__ac-trigger:hover .guests__ac-svg circle {
    stroke: #AA9DFA;
  }
  .playlist__btn:hover .playlist__btn-svg {
    opacity: 1;
    visibility: visible;
  }
  .playlist__btn:hover::before {
    background-color: #121723;
    opacity: 0.8;
  }
  .playlist__btn:hover + .playlist__info {
    background-color: #6D31EE;
    border-color: #6D31EE;
  }
  .playlist__btn:hover + .playlist__info .playlist__name {
    color: white;
  }
  .playlist__btn:hover + .playlist__info .playlist__text {
    color: white;
  }
  .product__btn:hover {
    color: #121723;
    background-color: #F0D288;
  }
  .product__btn:hover .product__btn-svg path:first-child {
    fill: #121723;
  }
  .product__btn:hover .product__btn-svg path:last-child {
    fill: #F0D288;
  }
  .about__input:hover {
    background-color: #292E39;
  }
  .footer__right-link:hover {
    color: #6D31EE;
  }
  .footer__socials-svg:hover path {
    fill: #6D31EE;
  }
  .nav-top__link:hover {
    color: #6D31EE;
  }
  .nav-bottom__link:hover {
    color: #6D31EE;
  }
  .nav-top-secondary__link:hover {
    color: #6D31EE;
  }
  .playlists__input:hover ~ .playlists__visible-checkbox {
    border-color: #6D31EE;
  }
  .broadcast__link:hover .broadcast__more {
    color: #6D31EE;
  }
  .broadcast__link:hover .broadcast__svg path {
    fill: #6D31EE;
  }
  .broadcast__link:hover .broadcast__svg-wrapper {
    margin-right: 0;
    padding-left: 6px;
  }
}
.header {
  background-color: white;
}
.header__tablet-top {
  display: flex;
}
.header__tablet-broadcast {
  display: none;
}
.header__top-wrapper {
  padding-top: 20px;
  padding-bottom: 21px;
  border-bottom: #E6E8EC 1px solid;
}
.header__bottom-wrapper {
  border-bottom: #E6E8EC 1px solid;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__bottom {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}
.header__tablet-wrapper {
  display: none;
}
.header__desktop-logo {
  display: flex;
}
.header__svg {
  width: 160px;
  height: 20px;
}
.header__nav-wrapper {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}
.header__search-btn-wrapper {
  max-width: 155px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.header__search {
  margin-right: 24px;
  position: relative;
}
.header__desktop-btn {
  padding: 10px 31px;
  border: solid #121723 2px;
  border-radius: 77px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
.header__desktop-btn:focus-visible {
  background-color: #AA9DFA;
  color: #121723;
  outline: none;
}
.header__desktop-btn:active {
  background-color: #4F28A5;
  color: white;
  border-color: #4F28A5;
}
.header__tablet-btn {
  display: none;
}
.header__tablet-btn:focus-visible {
  outline: none;
}
.header__desktop-broadcast {
  margin-right: 54px;
  padding: 10px 0;
  display: flex;
  align-items: center;
}
.header__secondary-broadcast {
  padding: 3px 48px 3px 13px;
  display: flex;
  align-items: center;
  border-right: #E6E8EC solid 1px;
  border-left: #E6E8EC solid 1px;
}
.header__primary-broadcast {
  padding: 3px 42px 3px 13px;
  display: flex;
  align-items: center;
  border-right: #E6E8EC solid 1px;
}
.header__broadcast-btn {
  display: none;
}
.header__broadcast-btn:focus-visible {
  outline: none;
}
.header__broadcast-btn:focus-visible .play-path {
  fill: #AA9DFA;
}
.header__broadcast-btn:active .play-path {
  fill: #4F28A5;
}
.header__broadcast-btn:focus-visible .pause-path {
  fill: #AA9DFA;
}
.header__broadcast-btn:active .pause-path {
  fill: #4F28A5;
}
.header__broadcast-btn .pause-path {
  display: none;
}
.header__broadcast-btn .pause-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.header__broadcast-btn rect {
  display: none;
}
.header__broadcast-btn .play-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.header__broadcast-btn.broadcast-btn--toggle .play-path {
  display: none;
}
.header__broadcast-btn.broadcast-btn--toggle rect {
  display: block;
}
.header__broadcast-btn.broadcast-btn--toggle .pause-path {
  display: block;
}
.header__ac {
  display: none;
}
.header__ac-svg {
  color: #A1A6B4;
  transition: color 0.2s ease-in-out;
}
.header__ac-trigger:focus-visible .header__ac-svg {
  color: #AA9DFA;
}
.header__ac-trigger:active .header__ac-svg {
  color: #4F28A5;
}
.header__tablet-logo:focus-visible {
  outline: 1px solid #6D31EE;
  outline-offset: 3px;
}
.header__desktop-logo:focus-visible {
  outline: 2px solid #6D31EE;
  outline-offset: 3px;
}
.header__ac-btn:focus-visible {
  outline: none;
}
.header__ac-btn:focus-visible svg circle {
  outline: none;
  fill: #AA9DFA;
}

.age {
  padding: 0 8px;
  display: flex;
  align-items: center;
  background-color: #E6E8EC;
}
.age__text {
  padding: 7px 4px;
  color: #A1A6B4;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16.3px;
  background-color: white;
  border-radius: 100%;
}

.burger {
  display: none;
}
.burger:focus-visible {
  outline: 2px solid #6D31EE;
  outline-offset: 3px;
}

.nav-top__close {
  width: 24px;
  height: 24px;
  display: none;
}
.nav-top__close:focus-visible {
  outline: solid 2px #6D31EE;
  outline-offset: 3px;
}
.nav-top__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.nav-top__item:not(:last-child) {
  margin-right: 59px;
}
.nav-top__link {
  font-weight: 400;
  line-height: 20px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.nav-top__link:focus-visible {
  color: white;
  background-color: #AA9DFA;
  outline: none;
}
.nav-top__link:active {
  color: #4F28A5;
}

.nav-top-secondary__list {
  display: none;
}

.nav-bottom {
  flex-grow: 1;
  margin-right: 52px;
  align-self: center;
}
.nav-bottom__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.nav-bottom__link:active {
  color: #4F28A5;
}
.nav-bottom__link:focus-visible {
  color: white;
  background-color: #AA9DFA;
  outline: none;
}
.nav-bottom__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.search__btn {
  width: 24px;
  height: 24px;
}
.search__btn:focus-visible {
  outline: none;
}
.search__btn:focus-visible svg path {
  stroke: #AA9DFA;
}
.search__btn:active svg path {
  stroke: #4F28A5;
}
.search__btn.search__btn--active {
  opacity: 0;
}
.search__btn svg path {
  stroke: #6D31EE;
  transition: stroke 0.2s ease-in-out;
}
.search__form {
  width: 325px;
  position: absolute;
  top: -1px;
  right: 0;
  display: flex;
  align-items: center;
  z-index: 14;
  background-color: white;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.search__form.search__form--active {
  opacity: 1;
  visibility: visible;
}
.search__input {
  background-color: white;
  flex-grow: 1;
  border: none;
  border-bottom: 1px solid #6D31EE;
  margin-right: 3px;
}
.search__input:focus-visible {
  outline: none;
}
.search__input::placeholder {
  font-weight: 400;
  line-height: 16px;
  font-size: 16px;
  color: #A1A6B4;
}
.search__submit {
  width: 24px;
  height: 24px;
}
.search__submit:focus-visible svg path {
  stroke: #AA9DFA;
}
.search__submit:focus-visible {
  outline: none;
}
.search__submit:active svg path {
  stroke: #4F28A5;
}
.search__submit svg path {
  stroke: #6D31EE;
  transition: stroke 0.2s ease-in-out;
}

.secondary-broadcast__button {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.secondary-broadcast__button:focus-visible {
  outline: none;
}
.secondary-broadcast__button:focus-visible .play-path {
  fill: #AA9DFA;
}
.secondary-broadcast__button:active .play-path {
  fill: #4F28A5;
}
.secondary-broadcast__button:focus-visible .pause-path {
  fill: #AA9DFA;
}
.secondary-broadcast__button:active .pause-path {
  fill: #4F28A5;
}
.secondary-broadcast__button .pause-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.secondary-broadcast__button .pause-path {
  display: none;
}
.secondary-broadcast__button rect {
  display: none;
}
.secondary-broadcast__button .play-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.secondary-broadcast__button.broadcast-btn--toggle .play-path {
  display: none;
}
.secondary-broadcast__button.broadcast-btn--toggle rect {
  display: block;
}
.secondary-broadcast__button.broadcast-btn--toggle .pause-path {
  display: block;
}
.secondary-broadcast__text-wrapper {
  display: flex;
  flex-direction: column;
}
.secondary-broadcast__top-text {
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  color: #A1A6B4;
}
.secondary-broadcast__bottom-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}

.primary-broadcast__button {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.primary-broadcast__button:focus-visible {
  outline: none;
}
.primary-broadcast__button:focus-visible .play-path {
  fill: #AA9DFA;
}
.primary-broadcast__button:active .play-path {
  fill: #4F28A5;
}
.primary-broadcast__button:focus-visible .pause-path {
  fill: #AA9DFA;
}
.primary-broadcast__button:active .pause-path {
  fill: #4F28A5;
}
.primary-broadcast__button .pause-path {
  display: none;
}
.primary-broadcast__button rect {
  display: none;
}
.primary-broadcast__button .play-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.primary-broadcast__button .pause-path {
  fill: #A1A6B4;
  transition: fill 0.2s ease-in-out;
}
.primary-broadcast__button.broadcast-btn--toggle .play-path {
  display: none;
}
.primary-broadcast__button.broadcast-btn--toggle rect {
  display: block;
}
.primary-broadcast__button.broadcast-btn--toggle .pause-path {
  display: block;
}
.primary-broadcast__text-wrapper {
  display: flex;
  flex-direction: column;
}
.primary-broadcast__top-text {
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  color: #A1A6B4;
}
.primary-broadcast__bottom-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}

@media (max-width: 1280px) {
  .header__desktop-logo {
    display: none;
  }
  .header__tablet-wrapper {
    padding: 23px 0;
    display: block;
    background-color: #E6E8EC;
  }
  .header__tablet-logo {
    display: flex;
  }
  .header__svg {
    width: 112px;
    height: 14px;
  }
  .header__secondary-broadcast {
    padding: 3px 25px 3px 13px;
  }
  .header__primary-broadcast {
    padding: 3px 18px 3px 12px;
  }
  .header__desktop-broadcast {
    margin-right: 0;
  }
  .nav-top {
    display: flex;
    margin-right: 35px;
    flex-grow: 1;
  }
  .nav-top__list {
    flex-grow: 1;
    justify-content: space-between;
  }
  .nav-top__item:not(:last-child) {
    margin-right: 10px;
  }
  .nav-bottom {
    margin-right: 34px;
  }
  .nav-bottom__item:not(:last-child) {
    margin-right: 10px;
  }
  .age {
    display: none;
  }
  .search__form {
    width: 340px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 114px;
  }
  .header__tablet-wrapper {
    padding: 0;
  }
  .header__tablet-logo {
    display: flex;
    align-items: center;
    margin-right: 77px;
  }
  .header__svg {
    width: 113px;
  }
  .header__secondary-broadcast {
    padding: 3px 49px 3px 13px;
  }
  .header__primary-broadcast {
    padding: 3px 42px 3px 12px;
  }
  .header__top-wrapper {
    padding-top: 22px;
  }
  .header__nav-wrapper {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    transform: translateY(-120%);
    background-color: #D2D5DD;
    visibility: hidden;
    overflow-y: auto;
    border: 1px solid black;
    background-image: url("img/burger1.png"), url("img/burger2.png");
    background-repeat: no-repeat;
    background-position: 60.9% bottom, 82.8% top;
    transition: visibility 0.4s ease-in-out, transform 0.4s ease-in-out;
  }
  .header__nav-wrapper--active {
    visibility: visible;
    transform: none;
  }
  .header__tablet-broadcast {
    padding: 10px 0;
    display: flex;
    align-items: center;
  }
  .header__desktop-broadcast {
    display: none;
  }
  .header__secondary-broadcast {
    border-color: #D2D5DD;
  }
  .header__primary-broadcast {
    border-color: #D2D5DD;
  }
  .header__broadcast-btn {
    display: flex;
    align-self: center;
    width: 20px;
    height: 20px;
  }
  .header__bottom-wrapper {
    padding: 12px 0;
  }
  .burger {
    display: block;
    position: relative;
    width: 24px;
    height: 24px;
  }
  .burger__line {
    position: absolute;
    left: 50%;
    width: 100%;
    height: 3px;
    border-radius: 4px;
    background-color: #6D31EE;
    transform: translateX(-50%);
    transition: background-color 0.2s ease;
  }
  .burger__line:first-child {
    top: 0;
  }
  .burger__line:nth-child(2) {
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
  .burger__line:last-child {
    bottom: 0;
  }
  .nav-top {
    padding: 28px 0 0 114px;
    margin-right: 0;
    flex-direction: column;
    justify-content: flex-start;
  }
  .nav-top__list {
    max-width: 250px;
    display: block;
    column-count: auto;
    column-gap: 35px;
    column-width: 95px;
  }
  .nav-top__item {
    margin-bottom: 51px;
  }
  .nav-top__item:not(:last-child) {
    margin-right: 0;
  }
  .nav-top__item:nth-last-child(2) {
    padding-top: 29px;
  }
  .nav-top__close {
    margin-bottom: 49px;
    display: flex;
  }
  .nav-bottom {
    margin-right: 27px;
  }
  .nav-bottom__item:not(:last-child) {
    margin-right: 0;
  }
}
@media (max-width: 750px) {
  .container {
    padding: 0 32px;
  }
  .header__top-wrapper {
    padding-top: 14px;
    padding-bottom: 13px;
  }
  .header__tablet-wrapper {
    padding: 9px 0;
  }
  .header__tablet-top {
    justify-content: space-between;
